import React from "react";
import { UserTopNav } from ".";
import { AppContext } from "../context";

interface Props {
    title: string;
    onMenuClick: () => void;
}

export const AppHeader: React.FC<Props> = ({ title, onMenuClick }) => {
    const { state } = React.useContext(AppContext);
    return (
        <header className="ss-dashboard-wrap-main-header">
            <div>
                <h1>
                    <span>
                        {
                            state.user?.CompanyNo &&
                            state.user?.EmployeeNo &&
                            <img
                                src={`${
                                    process.env.REACT_APP_DASHBOARD_URL as string
                                }/Root/Logo?company=${
                                    state.user?.CompanyNo
                                }&employee=${state.user?.EmployeeNo}`}
                                alt="Affinity - making work life easy"
                                height={48}
                            />
                        }

                        {
                            window.location.href.includes('testaffinitylogon') &&
                            <span>TE</span>
                        }

                    </span>
                </h1>
                <div>
                    <UserTopNav onMenuClick={onMenuClick} />
                </div>
            </div>
        </header>
    );
};
